.contacts {
    height: 100vh;
    background-color: #b484d5;
    display: flex;
    justify-content: center;
    align-items: center;
}

.title {
    background-color: dimgray;
}

.contactsContainer {
    width: 80%;
    height: 100%;
    border: 1px solid;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.form {
    background-color: dimgray;
    border: 1px solid;
    width: 287px;
    height: 40%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.text {
    width: 100%;
}

.button {
    background-color: dimgray;
    width: 60px;
}
